import { LoginViewModel } from './../models/login-view-model';
import { HttpBackend, HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import {map} from 'rxjs/operators';
import { JwtHelperService } from '@auth0/angular-jwt';

@Injectable({
  providedIn: 'root'
})
export class LoginService
{
  private httpClient: HttpClient;
  constructor(private httpBackend: HttpBackend, private jwtHelperService: JwtHelperService)
  {

  }

  currentUserName: string = null;

  public Login(loginViewModel: LoginViewModel): Observable<any>
  {
    this.httpClient = new HttpClient(this.httpBackend);
    return this.httpClient.post<any>('/api/Account/Login', loginViewModel, {responseType: 'json'})
    .pipe(map(user => {
      if (user){
        this.currentUserName = user.userInfo.UserName;
        sessionStorage.currentUser = JSON.stringify(user.token);
      }
      return user;
    }));
  }

  public Logout(): void
  {
    this.httpClient = new HttpClient(this.httpBackend);
    sessionStorage.removeItem('currentUser');
    this.currentUserName = null;
    const result = this.httpClient.post<any>('/api/Account/Logout', {responseType: 'json'});
  }

  public isAuthenticated(): boolean
  {
    const token = sessionStorage.getItem('currentUser') ? JSON.parse(sessionStorage.getItem('currentUser')) : null;
    if (this.jwtHelperService.isTokenExpired())
    {
      return false;
    }else{
      return true;
    }
  }

}
