import { CanActiveGuardService } from './services/can-active-guard.service';
import { LoginComponent } from './admin/login/login.component';
import { ProjectsComponent } from './admin/projects/projects.component';
import { AboutComponent } from './admin/about/about.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DashboardComponent } from './admin/dashboard/dashboard.component';

const routes: Routes = [
  {path: '', redirectTo: 'login', pathMatch: 'full'},
  {path: 'login', component: LoginComponent},
  {path: 'dashboard', component: DashboardComponent, canActivate: [CanActiveGuardService], data: {expectedRole: 'admin'}},
  {path: 'about', component: AboutComponent},
  {path: 'projects', component: ProjectsComponent, canActivate: [CanActiveGuardService], data: {expectedRole: 'admin'}}
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {useHash: true})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
